import * as S from "./style";
import { useState } from "react";
import { useGetSeriesList } from "queries/ComicUpload/Series/series.query";
import register from "assets/img/Upload/register.svg";
import usePopupModal from "utils/Modal/usePopupModal";
import SeriesRegisterModal from "components/Series/ComicSeriesRegister";
import { useNavigate } from "react-router-dom";

const SeriesItem = () => {
  const navigate = useNavigate();
  const [page] = useState(1);
  const { data: getSeriesList } = useGetSeriesList(page, { suspense: true });
  const { toggleSeriesAddModal, seriesAddModal } = usePopupModal();

  return (
    <S.SeriesContainer>
      <S.SeriesWrap
        style={{
          height:
            getSeriesList && getSeriesList?.seriesResponse.length > 0
              ? ""
              : "100vh",
        }}
      >
        <S.SeriesListBox>
          <S.SeriesListContainer>
            <S.TitleComponent>
              {getSeriesList && getSeriesList.seriesResponse.length > 0 ? (
                getSeriesList.seriesResponse.map((series) => {
                  return (
                    <S.SeriesBox>
                      <S.SeriesList
                        onClick={() => {
                          sessionStorage.setItem("SeriesId", `${series.id}`);
                          navigate(`/series/manage/${series.id}`);
                        }}
                      >
                        <S.BackgroundImgBox>
                          <S.BackgroundImg
                            alt="backgound"
                            src={series.titleMaker.backgroundImage}
                          />
                          <S.Gradient
                            style={{
                              background: `linear-gradient(#00000000 150px, ${series.titleMaker.color})`,
                            }}
                          ></S.Gradient>
                        </S.BackgroundImgBox>

                        <S.SeriesInfoBox>
                          <S.SeriesTitle>{series.title}</S.SeriesTitle>
                          <S.SeriesDesc>{series.description}</S.SeriesDesc>
                          <S.GenreBox>
                            {series.genre.map((genre) => (
                              <S.SeriesGenre>{genre}</S.SeriesGenre>
                            ))}
                          </S.GenreBox>
                        </S.SeriesInfoBox>
                      </S.SeriesList>
                    </S.SeriesBox>
                  );
                })
              ) : (
                <S.SeriesAddBox
                  onClick={() => {
                    toggleSeriesAddModal();
                  }}
                >
                  <img alt="modalicon" src={register} />
                  <S.SeriesAddTextTop>새 작품 등록</S.SeriesAddTextTop>
                  <S.SeriesAddTextBottom>
                    새로운 작품을 등록해보세요.
                  </S.SeriesAddTextBottom>
                </S.SeriesAddBox>
              )}
            </S.TitleComponent>
          </S.SeriesListContainer>
        </S.SeriesListBox>
      </S.SeriesWrap>
      <SeriesRegisterModal
        seriesAddModal={seriesAddModal}
        toggleSeriesAddModal={toggleSeriesAddModal}
      />
    </S.SeriesContainer>
  );
};

export default SeriesItem;
